/*==========================Question===========================*/
.tm-question-frame {
    width: 48%;
    min-width: 250px;
    box-shadow: grey 0 0 5px;
    margin-top: 20px;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.2s ease;
}

.tm-question-frame:hover {
    transform: scale(1.1);
    box-shadow: black 0 0 20px;
}

.tm-question-frame:hover .tm-qt-title {
    color: black;
}

.tm-qt-info {
    margin: 15px 20px 20px 20px;
}

.tm-img-db-icon {
    width: 70%;
}

.tm-qt-header {
    margin: 20px;
}

.tm-qt-header-icon {
    width: 30%;
}

.tm-qt-title {
    font-weight: bolder;
    font-size: 23px;
    line-height: 30px;
    height: 30px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    color: #537477;
    transition: all 0.5s ease;
}

.tm-qt-brief-content {
    font-weight: lighter;
    font-size: 13px;
    line-height: 20px;
    color: grey;
    height: 60px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.tm-qt-time {
    font-size: 10px;
    line-height: 12px;
    margin-top: 3px;
}

.tm-qt-info-detail {
    width: 25%;
}

.tm-qt-info-number {
    font-size: 25px;
    font-weight: bolder;
    color: grey;
}

.tm-qt-info-name {
    font-size: 14px;
    color: grey;
}

/*============================Footer===========================*/
.tm-footer-icon-row {
}

.tm-footer-icon-frame {
    height: 150px;
    background: linear-gradient(to top right, #B06AB3, #476ADB);
}

.tm-footer-icon {
    height: 40px;
    width: 40px;
    border-style: solid;
    border-width: 1px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 18px;
    transition: all ease 0.5s;
    border-radius: 40px;
    margin: 20px;
    border-color: white;
    color: white;
    cursor: pointer;
}

.tm-footer-icon-fb:hover {
    border-color: #006EE6;
    color: #006EE6;
    background-color: white;
}

.tm-footer-icon-twitter:hover {
    border-color: #1DA1F2;
    color: #1DA1F2;
    background-color: white;
}

.tm-footer-icon-youtube:hover {
    border-color: #f83f37;
    color: #f83f37;
    background-color: white;
}

.tm-footer-icon-ig:hover {
    border-color: #F2B25F;
    color: #F2B25F;
    background-color: white;
}

.tm-footer-icon-mail:hover {
    border-color: #A9443B;
    color: #A9443B;
    background-color: white;
}

/*============================Side bar=========================*/
.tm-sidebar-left-frame {
    position: absolute;
    left: 5px;
    top: 30px;
    width: 17%;
}

.tm-sidebar-right-frame {
    position: absolute;
    right: 5px;
    top: 30px;
    width: 17%;
    z-index: -1;
}

.tm-avatar-sidebar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
    margin: 20px 0;
}

.tm-sidebar-name {
    font-weight: bolder;
}

.tm-sidebar-detail-info {
    background: linear-gradient(#F4AC39, #F4AC39 59%, white 59%, white 61%, #FECD3D 61% );
}

.tm-sidebar-question-info {
    background: linear-gradient(#69AFBA, #69AFBA 59%, white 59%, white 61%, #7EC6D9 61% );
}

.tm-sidebar-answer-info {
    background: linear-gradient(#8DC970, #8DC970 59%, white 59%, white 61%, #B6DA97 61% );
}

.tm-sidebar-question-info, .tm-sidebar-answer-info, .tm-sidebar-detail-info {
    margin: 20px 0;
    width: 120px;
    height: 120px;
    border-radius: 10px;
}

.tm-sidebar-number {
    font-size: 25px;
    font-weight: bolder;
    text-align: center;
    width: 100%;
    height: 60%;
}

.tm-sidebar-title {
    font-size: 15px;
    height: 40%;
    width: 100%;
    text-align: center;
}

.tm-sidebar-number, .tm-sidebar-title {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.tm-topuser-img {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    object-fit: cover;
    float: left;
    margin: 0 5px;
    cursor: pointer;
}

.tm-topuser-name {
    line-height: 30px;
    font-weight: lighter;
    font-size: 13px;
    cursor: pointer;
    text-decoration: none;
    color: black;
    transition: all ease 0.2s;
}

.tm-topuser-name:hover {
    font-weight: bold;
}

.tm-sidebar-right-title {
    margin: 10px;
    font-size: 20px;
}

.tm-sidebar-right-deco {
    width: 60%;
    height: 2px;
    background: black;
    margin: 0 10px;
}

.tm-sidebar-frame {
    margin-bottom: 30px;
}



/*=============================Form================================*/
.tm-form-container {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(117, 141, 162, 0.7);
    filter: blur(23);
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.tm-form-frame {
    width: 80%;
    height: auto;
    position: relative;
    margin: auto;
    background-color: white;
    padding: 30px;
    border-radius: 10px;
}

.tm-form-avatar {
    width: 40px;
    height: 40px;
    object-fit: contain;
    border-radius: 50%;
}

.tm-form-info {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 15px;
}

.tm-form-name {
    font-size: 16px;
    line-height: 25px;
}

.tm-form-statistic {
    font-size: 10px;
    color: darkgray;
    line-height: 15px;
}

.tm-form-count {
    color: gray;
    margin-left: 5px;
    margin-right: 10px;
    cursor: pointer;
}

.tm-form-content {
    font-size: 13px;
    line-height: 24px;
    margin: 10px 0;
    max-height: 250px;
    overflow: auto;
    text-align: justify;
}

/* =================================Back ground=============================== */
.tm-background-radius-20 {
    background-color: #ecebeb;
    padding: 20px 0;
    border-radius: 20px;
}